export default {
	data() {
		return {
			imgsrc:''
		}
	},
	onLoad(options){
		console.log(options.query)
		let codeUrl;
		if(options.query==1){
			
		}
		codeUrl=uni.getStorageSync('codeUrl');
		this.imgsrc=codeUrl
	},
	methods: {
		join(){
			uni.showModal({
				title: '温馨提示',
				cancelText:'否',
				confirmText:'是',
				content: '商家是否扫码关注公众号',
				success: function (res) {
					if (res.confirm) {
						console.log('用户点击确定');
						uni.switchTab({
							url: '/pages/home/home'
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});

		}
	}
}
